.navigationWrapper {
  width: 50%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.navigationButtonDisabled {
  text-align: center;
  border-radius: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: transparent;
  border-style: none;
  flex: 1 1;
  color: hsl(208, 7%, 56%, 0.3);
  font-size: 2rem;
}

.navigationButton {
  text-align: center;
  border-radius: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: transparent;
  border-style: none;
  flex: 1 1;
  color: hsl(208, 7%, 56%);
  font-size: 2rem;
  -webkit-transition: all 100ms ease-in-out;
  -o-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
  margin: 0;
  padding: 0;
}

.navigateHome {
  color: hsl(208, 7%, 56%);
  font-size: 2rem;
}

.navigateHome:hover {
  color: hsl(208, 7%, 26%);
}

.navigationButton:hover {
  color: hsl(208, 7%, 26%);
  -webkit-box-shadow: 0.1rem 0.1rem 0.5rem 0 hsla(0, 0%, 0%, 0.75);
          box-shadow: 0.1rem 0.1rem 0.5rem 0 hsla(0, 0%, 0%, 0.75);
}
.navigationButton:active,
.navigateHome:active {
  outline: none;
  color: hsl(208, 7%, 16%);
}



