.container {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.card {
  position: relative;
  max-width: 470px;
  max-height: 100%;
  margin: 4% auto;
  display: -ms-flexbox;
  display: flex;
  padding: 6%;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 0.2rem;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.card:hover {
  top: -2px;
  -webkit-box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

/* Beer card */

.beer-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-bottom: 5%;
}

.beer-card-title {
  text-align: left;
  border-bottom: 0.05rem solid hsla(0, 0%, 30%, 0.7);
}

.beer-card-info {
  width: 80%;
}

.beer-card-img {
  width: 8%;
  margin-left: 7%;
  margin-top: 7%;
}

.showmorebutton {
  color: rgb(50, 161, 165);
}

/* Food card */

.food-card {
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.food-info {
  padding-left: 4%;
  margin-top: 4%;
  margin-bottom: 4%;
  font-size: 0.7rem;
}

.food-card-info {
  width: 100%;
}

.food-card-button {
  padding-bottom: 4%;
}

.food-card-title {
  text-align: left;
  border-bottom: 0.05rem solid hsla(0, 0%, 30%, 0.7);
}

.food-card-intro {
  font-size: 0.8rem;
  padding-top: 3%;
  padding-bottom: 3%;
}

.food-card-img {
  margin-bottom: 5%;
  margin-top: 5%;
  border-radius: 0.2rem;
  border: 0.05rem solid hsla(0, 0%, 30%, 0.7);
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.food-card-button {
  -ms-flex-pack: center;
      justify-content: center;
}

/* General */

.beer-card-description,
.food-card-description {
  margin-top: 5%;
  font-size: 0.8rem;
}

.beer-name,
.food-name {
  font-size: 1rem;
  font-weight: bold;
}

.beer-card-img img,
.food-card-img img {
  width: 100%;
}

.badge {
  margin-bottom: 2%;
}

.alert {
  margin: 0;
}

.divider-hidden {
  display: none;
}

.divider-show {
  display: block;
  width: 90%;
  border-bottom: 0.05rem solid hsla(0, 0%, 30%, 0.7);
  margin: 2% auto;
}

.resultsHeader {
  width: 90%;
  margin: 1rem auto;
  padding: 5% 0;
  background-color: hsl(325, 59%, 49%);
  -webkit-transform: rotate(-5deg);
      -ms-transform: rotate(-5deg);
          transform: rotate(-5deg);
  border-radius: 0.15rem;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.resultsHeader h3 {
  color: hsla(0, 0%, 100%, 0.9);
  font-weight: bold;
  margin: 0.5rem 0;
  font-size: 1.6rem;
  text-transform: uppercase;
  text-align: center;
  -webkit-transform: rotate(2deg);
      -ms-transform: rotate(2deg);
          transform: rotate(2deg);
}

@media screen and (min-width: 650px) {
  .resultsHeader {
    width: 100%;
    padding: 2% 0;
    margin: 2rem 0;
    background-color: hsl(325, 59%, 49%);
    -webkit-transform: rotate(-2.5deg);
        -ms-transform: rotate(-2.5deg);
            transform: rotate(-2.5deg);
  }
}



